@import url(https://fonts.googleapis.com/css?family=Lato:400,900);
@import url(https://fonts.googleapis.com/css2?family=Lato:ital@1&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.main-content {
  float: right;
  margin-left: 30%;
  height: 100vh;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

#sidebar {
    position: absolute;
    background: white;
    float: left;
    left: 160px;
    top: 0px;
    width: 250px;
    height: 90%;
    border: 1px solid rgba(0, 0, 0, 0.1);
    z-index: 1001;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

#sidebar-list {
    margin-top: 10px;
}

#squareimg {
    width: 200px;
    height: 190px;
    top: 40px;
    left: 3px;
    overflow: hidden;
    border-radius: 50%;
    position: relative;
    display: inline-block;
}

#sidebar-title {
    font-size: 32px;
}

#sidebar-footer {
    position: absolute;
    bottom: 0;
    width: 100%;
    text-align: center;
}

.sidebar-link:hover {
    transition: 0.8s;
    background-color: #0099FF;
    color: white;
}

.active {
    background-color: #0099FF;
    color: white;
}

.semicircle-pic {
    width: 100%;
    height: auto;
}

ul {
    margin: 0;
    padding: 0;
}

ul li {
    list-style-type: none;
    margin: 0;
}

ul li a {
    display: block;
    text-decoration: none;
}


.entry {
    text-align: left;
}

.projects {
    padding-left: 3%;
    white-space: pre-wrap;
    padding-right: 10%;
}

.projHeader {
    text-align: left;
}

.about {
    padding-left: 3%;
    white-space: pre-wrap;
}

.about h1 {
    text-align: left;
}

.aboutPara {
    padding-right: 3em;
    text-align: left;
}

.aboutHeader {}
#clock {
    font-family: Lato, sans-serif;
    font-size: 6rem;
    letter-spacing: .05em;
}

#center_clock {
    width: 100%;
    text-align: center;
}

.bookmark-link {
    display: block;
}

.bookmark-link:hover {
    transition: 0.8s;
    background-color: #0099FF;
}

.bookmark-header {
    font-family: 'Lato', sans-serif;
    font-size: 16px;
    padding-bottom: 3px; 
    margin: 3px;
    background: #0099FF;
    color: aliceblue;
}

.bookmark-col {
    display: table-cell;
    min-width: 15vw;
}

#searchbar {
    width: 80%;
    font-size: 3em;
    background-color: transparent;
    border: none;
    border-bottom: 1px solid black;
    outline: none;
    text-align: center;
}

.search-suggestions {
    display: inline-block;
    margin: 20px 100px;
}

#search-form {
    text-align: center;
}

.wave {
    position: absolute;
    bottom: 0;
    width: 100%;
    height: 100px;
    background: url(/static/media/wave.ee93708c.svg);
    background-size:990px 100px;
    background-repeat: repeat-x;
}

.wave1 {
    animation:animate 30s linear infinite;
    z-index:1000;
    opacity:1;
    animation-delay:0s;
    bottom: 0;
}

.wave2 {
    animation:animate2 15s linear infinite;
    z-index:999;
    opacity:0.5;
    animation-delay:-5s;
    bottom: 10px;
}

.wave3 {
    animation:animate 30s linear infinite;
    z-index:998;
    opacity:0.2;
    animation-delay:-2s;
    bottom: 15px;
}

.wave4 {
    animation:animate2 5s linear infinite;
    z-index:997;
    opacity:0.7;
    animation-delay:-5s;
    bottom: 20px;
}

@keyframes animate{
	0%{
		background-position-x: 0;
	}
	100%{
		background-position-x: 990px;
	}
}

@keyframes animate2{
	0%{
		background-position-x: 0;
	}
	100%{
		background-position-x: -990px;
	}
}

.startpage {
    width: 71vw;
    height: 100vh;
    /*background: #1A1E23;*/
}

.startpage-table {
    display: table;
    margin: 0 auto;
    table-layout: fixed;
}

.home {
    position: absolute;
    top: 20%;
    left: 50%;
}

.penguin {
    width: 300px;
}
